.t1.page-homepage {
    position: relative;
}

.emergencynotice-section {
    padding: 2rem 0;
    border-radius: 0;
    position: relative;
    background-repeat: no-repeat;
    background-color: #C6401D;
    background-image: linear-gradient(60deg, #C6401D 20%, #C6671D 100%);
    background-blend-mode: color-dodge;
    background-position-y: 80%;
    background-size: 300%;
    background-size: cover;
    background-blend-mode: normal;
}

.emergencynotice-section h1 {
    margin-left: auto;
    margin-right: auto;
}

.t1.page-california-state-university-sacramento div[id="sacens"] {
    background: #900;
    top: 0;
    left: 0;
    z-index: 1000;
    width: 100%;
    padding: 3em 0 0 0;
    -webkit-box-shadow: #ccc 3px 8px 10px;
    box-shadow: #ccc 3px 8px 10px;
    border-bottom: 9px solid #cbb668;
    margin-bottom: 2em;
}

.t1.page-california-state-university-sacramento div[id="sacens"] div[id="sac_ens_msg"],
.emergencynotice-section .emergencynotice-msg {
    padding-bottom: 2em;
    width: 80%;
    margin: 0 auto;
}

.t1.page-california-state-university-sacramento div[id="sacens"] div[id="sac_ens_msg"] h1,
.t1.page-california-state-university-sacramento div[id="sacens"] div[id="sac_ens_msg"] h2,
.emergencynotice-section h1,
.emergencynotice-section h2 {
    color: #fff;
    font-size: 36px;
    font-weight: 500;
    text-shadow: rgba(0, 0, 0, .3) 3px 3px 3px;
}

.t1.page-california-state-university-sacramento div[id="sacens"] div[id="sac_ens_msg"] p,
.t1.page-california-state-university-sacramento div[id="sacens"] div[id="sac_ens_msg"] span,
.emergencynotice-section p,
.emergencynotice-section li {
    color: #fff !important;
    font-size: 16px;
}

.t1.page-california-state-university-sacramento div[id="sacens"] div[id="sac_ens_msg"] a,
.emergencynotice-section a {
    color: #f0e1b0;
    /* Web 5 */
    text-decoration: underline;
}

.t1.page-california-state-university-sacramento div[id="sacens"] div[id="sac_ens_msg"] a:hover,
.emergencynotice-section a:hover {
    color: #cbb668;
    /* Web 3 */
    text-decoration: none;
}


