/*** ======= Marquee and News Sections ======== ***/

.sac-state-news-section {
    ul.sac-state-news {
        padding: 0;
        -ms-flex-wrap: wrap;
        flex-wrap: wrap;
        max-width: 1140px;
        li.news-box {
            /*padding: 0 1% 0 0;*/
            /*    -webkit-box-flex: 1;*/
            -ms-flex: 1 0 100%;
            -webkit-box-flex: 1;
            flex: 1 0 100%;
            list-style: none;
            margin-bottom: 2vh;
            @media screen and (min-width: 900px) {
                -ms-flex: 1 0 32%;
                -webkit-box-flex: 1;
                flex: 1 0 32%;
                margin-right: 2%;
                &:last-of-type {
                    margin-right: 0;
                    padding: 0;
                }
            }
        }
    }
}

.news-box {
    div.news-box-content {
        background-color: #043927;
        background-color: rgba(0, 78, 56, 1);
        border-radius: 4px;
        height: 100%;
        padding: 0 0 1em 0;
        -webkit-box-shadow: rgb(97, 84, 34) 0 3px 6px;
        box-shadow: rgb(97, 84, 34) 0 3px 6px;
        div.img-wrapper {
            background: #000;
            text-align: center;
            @media screen and (min-width: 900px) {
                max-height: 140px;
                overflow: hidden;
            }
            @media screen and (min-width:1000px) {
                max-height: 180px;
            }
            @media screen and (min-width:1200px) {

                max-height: 220px;
            }
        }
        img {
            width: 100%;
            max-width: 100%;
            margin: 0 auto;
            object-fit: contain;
        }
    }
    h3.news-box-heading {
        font-size: 18px;
        padding: 1rem 10% 0;
        color: #f0e1b0;
    }
    p {
        padding: .5rem 10% 1rem;
        color: #fff;
        font-weight: normal;
    }
}
